import * as React from 'react';
import { AiOutlineEye, AiOutlineEyeInvisible } from 'react-icons/ai';

type InputWithLogoProps = {
  label: string;
  id: string;
  name: string;
  type: string;
  placeholder: string;
  passwordIsVisible?: boolean;
  confirmPasswordIsVisible?: boolean;
  setPasswordVisible?: React.Dispatch<React.SetStateAction<boolean>>;
  setConfirmPasswordVisible?: React.Dispatch<React.SetStateAction<boolean>>;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  formData: { [key: string]: string };
  errors: { [key: string]: string | null };
  leftIcon: React.ReactNode;
};

export default function InputWithLogo(props: InputWithLogoProps) {
  return (
    <React.Fragment>
      <label
        htmlFor={props.id}
        className="relative">
        {props.label} <span className="text-red-500">*</span>
        {props.leftIcon}
        <input
          id={props.id}
          name={props.name}
          type={props.type}
          className={`${props.errors[props.name] ? 'border-red-500' : 'border-gray-300'} border-2 mt-2 shadow-sm rounded-md w-full px-8 py-2.5 focus:outline-none focus:border-primary-500`}
          placeholder={props.placeholder}
          autoComplete="off"
          onChange={props.onChange}
          onPaste={() => event.preventDefault()}
          value={props.formData[props.name]}
        />
        {props.passwordIsVisible && props.name === 'password' &&
          <AiOutlineEye
            className="w-6 h-6 absolute top-[85%] transform -translate-y-1/2 right-2 cursor-pointer"
            onClick={() => props.setPasswordVisible?.((prevData) => !prevData)}
          />}
        {!props.passwordIsVisible && props.name === 'password' &&
          <AiOutlineEyeInvisible
            className="w-6 h-6 absolute top-[85%] transform -translate-y-1/2 right-2 cursor-pointer"
            onClick={() => props.setPasswordVisible?.((prevData) => !prevData)}
          />}
        {props.confirmPasswordIsVisible && props.name === 'confirm_password' &&
          <AiOutlineEye
            className="w-6 h-6 absolute top-[85%] transform -translate-y-1/2 right-2 cursor-pointer"
            onClick={() => props.setConfirmPasswordVisible?.((prevData) => !prevData)}
          />}
        {!props.confirmPasswordIsVisible && props.name === 'confirm_password' &&
          <AiOutlineEyeInvisible
            className="w-6 h-6 absolute top-[85%] transform -translate-y-1/2 right-2 cursor-pointer"
            onClick={() => props.setConfirmPasswordVisible?.((prevData) => !prevData)}
          />}
      </label>
      {props.errors[props.name] && <p className="text-red-500 mt-1">{props.errors[props.name]}</p>}
    </React.Fragment>
  );
};