import * as React from 'react';
import { Button } from '@material-tailwind/react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useGoogleLogin, GoogleLogin } from '@react-oauth/google';
import { rc4_encrypt_object, rc4_decrypt_object } from '../../../utils/encryptions';
import { toast } from 'react-toastify';
import apiService from '../../../utils/services';
import sessions from '../../../utils/sessions';
// import googleLogo from '../assets/images/logo/google.png';
import { Base64 } from 'js-base64';

// Define the menu item type
type T_MenuItem = {
  label: string;
  link: string;
  icon?: string;
  children?: T_MenuItem[];
};

export default function ButtonLoginWithGoogle() {
  const { executeRecaptcha } = useGoogleReCaptcha();
  const navigate = useNavigate();
  const location = useLocation();
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);
  const [isLoading, setIsLoading] = React.useState(false);

  // Function to sorting menu
  const sortMenu = (menu: any[]): any[] => {
    return menu.sort((a, b) => parseInt(a.sort) - parseInt(b.sort));
  };

  // Function to map menu
  const mapMenu = (menu: any[]): T_MenuItem[] => {
    return sortMenu(menu).map((item) => ({
      label: item.name,
      link: '/' + item.url,
      icon: item.icon,
      children: item.subMenu ? mapMenu(item.subMenu) : undefined,
    }));
  };

  const getDataMenu = async (id) => {
    try {
      const result = await apiService.post('/master/menu', { company_id: id });
      // Use the function to create the menuItems array
      const menuItems: T_MenuItem[] = mapMenu(result.data);
      localStorage.setItem('menus', JSON.stringify(menuItems));
      return menuItems;
    } catch (error) {
      errorNotify(error);
    };
  };

  const onLoginWithGoogle = useGoogleLogin({
    onSuccess: async tokenResponse => {
      try {
        const response = await fetch("https://www.googleapis.com/oauth2/v3/userinfo", {
          method: 'POST',
          headers: { Authorization: `Bearer ${tokenResponse.access_token}` },
        });
        const resultLoginGoogle = await response.json();
        if (resultLoginGoogle.email) {
          if (!executeRecaptcha) {
            errorNotify("Execute recaptcha not yet available");
            return;
          };
          executeRecaptcha("enquiryFormSubmit")
            .then(async (gRecaptchaToken) => {
              const resultRecaptcha: any = await onLogin(gRecaptchaToken, resultLoginGoogle.email, resultLoginGoogle.name);
              if (resultRecaptcha.status == true) {
                successNotify(resultRecaptcha.message);
              } else {
                errorNotify(resultRecaptcha.message);
              };
            });
        };
      } catch (error) {
        errorNotify('Terjadi Kesalahan! Silakan coba lagi nanti..');
      };
    }
  });

  const onLogin = (async (gRecaptchaToken: string, email: string, name: string) => {
    const encryptLogin = rc4_encrypt_object({ email, gRecaptchaToken, provider: 'google', hostkey: Base64.encode(window.location.origin) });
    const encryptRegister = rc4_encrypt_object({
      email, name, provider: 'google', is_company: 0, company_id: '', organization_id: '', gRecaptchaToken, hostKey: Base64.encode(window.location.origin)
    });
    setIsLoading(true);
    try {
      let response = { data: '', message: '' };
      if (location.pathname.includes('login')) {
        response = await apiService.post('/auth/login', { data: encryptLogin });

        console.log('🐙 ~ executeRecaptcha ~ response:', response);
        const encryptData = response.data;
        const token = rc4_decrypt_object(encryptData).token;
        const user = JSON.stringify(rc4_decrypt_object(encryptData).data);
        sessions.saveSession(token, user);
        successNotify('Login successfully');

        /**
         * Cek localstorage dengan key vacany_id
         * bila ada maka arahkan ke halaman career/vacancy_id
         */

        if (JSON.parse(user || '{}').userlevel === 'BE557620828667306C6921AB48D52F54') {
          /**
           * Pengkondisian direct ke halaman career bila ada vacancy_id di localstorage
           */
          const vacancy_id = localStorage.getItem('vacancy_id');
          if (vacancy_id) return navigate(`/career/${vacancy_id}`);

          navigate('/my-application');
        } else {
          const menuItems = await getDataMenu(rc4_decrypt_object(encryptData).data.company_id);
          if (menuItems?.length) {
            navigate('/dashboard');
          };
        };
      } else {
        response = await apiService.post('/auth/register', { data: encryptRegister });
        successNotify(response.message);
      };
    } catch (error) {
      errorNotify(error.response.data.message);
    } finally {
      setIsLoading(false);
    };
  });

  const handleLoginSuccess = (credentialResponse) => {
    const idToken = credentialResponse.credential;

    // Send the ID token to the backend
    fetch('https://apiwr.infomedia.co.id/auth/google/callback', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ token: idToken }),
    })
      .then(response => response.json())
      .then(async response => {
        if (location.pathname.includes('login')) {
          const encryptData = response.data;
          const token = rc4_decrypt_object(encryptData).token;
          const user = JSON.stringify(rc4_decrypt_object(encryptData).data);
          sessions.saveSession(token, user);
          successNotify('Login successfully');

          /**
           * Cek localstorage dengan key vacany_id
           * bila ada maka arahkan ke halaman career/vacancy_id
           */

          if (JSON.parse(user || '{}').userlevel === 'BE557620828667306C6921AB48D52F54') {
            /**
             * Pengkondisian direct ke halaman career bila ada vacancy_id di localstorage
             */
            const vacancy_id = localStorage.getItem('vacancy_id');
            if (vacancy_id) return navigate(`/career/${vacancy_id}`);

            navigate('/my-application');
          } else {
            const menuItems = await getDataMenu(rc4_decrypt_object(encryptData).data.company_id);
            if (menuItems?.length) {
              navigate('/dashboard');
            };
          };
        } else {
          successNotify(response.message);
        };
      })
      .catch(error => {
        console.error('Error:', error);
      });
  };

  return (
    // <Button
    //   loading={isLoading}
    //   type="button"
    //   variant="outlined"
    //   onClick={onLoginWithGoogle}
    //   className="normal-case border-gray-500 flex justify-center items-center gap-1 py-2 hover:border-primary-500"
    // >
    //   <img src={googleLogo} alt="Google logo" width='24' /> <span>{location.pathname === '/login' ? 'Continue' : 'Register'} with Google</span>
    // </Button>
    <div className="flex justify-center items-center">
      <GoogleLogin
        onSuccess={handleLoginSuccess}
        onError={() => errorNotify('Login gagal, silahkan coba lagi nanti..')}
      />
    </div>
  );
};