import { Buffer } from 'buffer';
const RC4_KEY = 'WrPr@duCt@2024!!=';

export function rc4_encrypt(data: string | any[] | Buffer, key: string) {
  let i = 0;
  let j = 0;
  const S = new Array(256);
  const result: any = [];

  // Initialize the state array (S) based on the key
  for (let i = 0; i < 256; i++) {
    S[i] = i;
  }
  for (let i = 0; i < 256; i++) {
    j = (j + S[i] + key.charCodeAt(i % key.length)) % 256;
    [S[i], S[j]] = [S[j], S[i]];
  }

  // Encrypt each byte of the data
  for (let k = 0; k < data.length; k++) {
    i = (i + 1) % 256;
    j = (j + S[i]) % 256;
    [S[i], S[j]] = [S[j], S[i]];
    const t = (S[i] + S[j]) % 256;
    const keystreamByte = S[t];
    result.push(data[k] ^ keystreamByte);
  }

  // Convert result to string (assuming data is string)
  return Buffer.from(result).toString('hex');
}

export function rc4_decrypt(encryptedHex: WithImplicitCoercion<string> | { [Symbol.toPrimitive](hint: "string"): string; }) {
  // Convert the hexadecimal string to a Buffer
  const encryptedBytes = Buffer.from(encryptedHex, 'hex');

  let i = 0;
  let j = 0;
  const S = new Array(256);
  const result: any = [];

  // Initialize the state array (S) based on the key
  for (let i = 0; i < 256; i++) {
    S[i] = i;
  }
  for (let i = 0; i < 256; i++) {
    j = (j + S[i] + RC4_KEY.charCodeAt(i % RC4_KEY.length)) % 256;
    [S[i], S[j]] = [S[j], S[i]];
  }

  // Decrypt each byte of the data
  for (let k = 0; k < encryptedBytes.length; k++) {
    i = (i + 1) % 256;
    j = (j + S[i]) % 256;
    [S[i], S[j]] = [S[j], S[i]];
    const t = (S[i] + S[j]) % 256;
    const keystreamByte = S[t];
    result.push(encryptedBytes[k] ^ keystreamByte);
  }

  // Convert result to string (assuming data is string)
  return Buffer.from(result).toString('utf-8');
}

export function rc4_encrypt_object(obj: any) {
  // Convert the object to a JSON string
  const jsonString = JSON.stringify(obj);
  // Encrypt the JSON string using RC4
  return rc4_encrypt(Buffer.from(jsonString, 'utf-8'), RC4_KEY);
}

export function rc4_decrypt_object(encryptedHex: any) {
  // Decrypt the encrypted data using RC4
  const decryptedJsonString: any = rc4_decrypt(encryptedHex);
  // Parse the decrypted JSON string into an object
  return JSON.parse(decryptedJsonString);
}